___CSS_LOADER_EXPORT___.push([module.id, ".disabled{pointer-events:none}", "",{"version":3,"sources":["webpack://./resources/js/pages/talents/index.vue"],"names":[],"mappings":"AAiQA,UACA,mBACA","sourcesContent":["<template>\r\n    <div>\r\n        <page-heading title=\"talents\" />\r\n        <div v-if=\"TALENTS.data && !loading\">\r\n            <div class=\"row mb-2\">\r\n                <div class=\"col-12 d-flex justify-content-between\">\r\n                    <div class=\"dropdown\">\r\n                        <button\r\n                            class=\"btn btn-info text-white btn-sm dropdown-toggle\"\r\n                            type=\"button\"\r\n                            id=\"dropdownSort\"\r\n                            data-mdb-toggle=\"dropdown\"\r\n                            aria-expanded=\"false\"\r\n                        >\r\n                            Sort By\r\n                        </button>\r\n                        <ul\r\n                            class=\"dropdown-menu\"\r\n                            aria-labelledby=\"dropdownSort\"\r\n                        >\r\n                            <li\r\n                                v-for=\"(item, index) in sortBySelection\"\r\n                                :key=\"index\"\r\n                            >\r\n                                <a\r\n                                    class=\"dropdown-item\"\r\n                                    :class=\"{\r\n                                        active: currentSort(item.name, item.dir)\r\n                                    }\"\r\n                                    href=\"javascript:void(0)\"\r\n                                    @click=\"sortTalents(item.name, item.dir)\"\r\n                                    >{{ item.name }} - {{ item.dir }}</a\r\n                                >\r\n                            </li>\r\n                        </ul>\r\n                    </div>\r\n                    <router-link\r\n                        :to=\"{ name: 'talents-create' }\"\r\n                        type=\"button\"\r\n                        class=\"btn btn-primary btn-sm btn-rounded\"\r\n                        ><i class=\"ri-user-add-line me-2\"></i>Create\r\n                        Talent</router-link\r\n                    >\r\n                </div>\r\n            </div>\r\n            <div class=\"table-responsive\">\r\n                <table class=\"table table-hover align-middle\">\r\n                    <thead class=\"table-dark\">\r\n                        <tr class=\"text-nowrap\">\r\n                            <th scope=\"col\">ID</th>\r\n                            <th scope=\"col\">Name</th>\r\n                            <th scope=\"col\">Position</th>\r\n                            <th scope=\"col\">University</th>\r\n                            <th scope=\"col\">Created By</th>\r\n                            <th scope=\"col\">Created At</th>\r\n                            <th scope=\"col\"></th>\r\n                        </tr>\r\n                    </thead>\r\n                    <tbody>\r\n                        <tr v-for=\"item in TALENTS.data\" :key=\"item.id\">\r\n                            <th scope=\"row\">{{ item.id }}</th>\r\n                            <td>{{ item.name }}</td>\r\n                            <td>{{ item.applied_position }}</td>\r\n                            <td>\r\n                                {{ item.university }}\r\n                            </td>\r\n                            <td>{{ item.pic.name }}</td>\r\n                            <td>\r\n                                {{\r\n                                    item.dates.created_at\r\n                                        | moment(\"dddd, DD/MM/YYYY\")\r\n                                }}\r\n                            </td>\r\n                            <td>\r\n                                <div class=\"d-flex justify-content-end\">\r\n                                    <router-link\r\n                                        :to=\"{\r\n                                            name: 'talents-show',\r\n                                            params: { id: item.id }\r\n                                        }\"\r\n                                        type=\"button\"\r\n                                        class=\"btn btn-info text-white btn-sm px-3 mx-2 d-flex align-items-center\"\r\n                                    >\r\n                                        <i class=\"ri-eye-line me-2\"></i>\r\n                                        <span>Details</span>\r\n                                    </router-link>\r\n                                </div>\r\n                            </td>\r\n                        </tr>\r\n                    </tbody>\r\n                    <tfoot>\r\n                        <tr>\r\n                            <td colspan=\"7\">\r\n                                <div class=\"d-flex justify-content-between\">\r\n                                    <div>\r\n                                        <p>\r\n                                            Page\r\n                                            {{ TALENTS.meta.current_page }} of\r\n                                            {{ TALENTS.meta.last_page }}\r\n                                        </p>\r\n                                    </div>\r\n                                    <nav aria-label=\"pagination\">\r\n                                        <ul\r\n                                            class=\"pagination justify-content-end mb-0\"\r\n                                        >\r\n                                            <li\r\n                                                class=\"page-item\"\r\n                                                :class=\"{\r\n                                                    disabled:\r\n                                                        !TALENTS.links.prev &&\r\n                                                        !loading\r\n                                                }\"\r\n                                                :disabled=\"loading\"\r\n                                                @click=\"changePage('prev')\"\r\n                                            >\r\n                                                <a\r\n                                                    class=\"page-link\"\r\n                                                    href=\"#\"\r\n                                                    tabindex=\"-1\"\r\n                                                    >Previous</a\r\n                                                >\r\n                                            </li>\r\n                                            <li class=\"page-item active\">\r\n                                                <a\r\n                                                    class=\"page-link pe-none\"\r\n                                                    href=\"javascript:void(0)\"\r\n                                                    >{{\r\n                                                        TALENTS.meta\r\n                                                            .current_page\r\n                                                    }}</a\r\n                                                >\r\n                                            </li>\r\n                                            <li\r\n                                                class=\"page-item\"\r\n                                                :class=\"{\r\n                                                    disabled:\r\n                                                        !TALENTS.links.next &&\r\n                                                        !loading\r\n                                                }\"\r\n                                                :disabled=\"loading\"\r\n                                                @click=\"changePage('next')\"\r\n                                            >\r\n                                                <a\r\n                                                    class=\"page-link\"\r\n                                                    href=\"javascript:void(0)\"\r\n                                                    >Next</a\r\n                                                >\r\n                                            </li>\r\n                                        </ul>\r\n                                    </nav>\r\n                                </div>\r\n                            </td>\r\n                        </tr>\r\n                    </tfoot>\r\n                </table>\r\n            </div>\r\n        </div>\r\n        <div v-else>\r\n            <div class=\"row\">\r\n                <div class=\"col d-flex justify-content-center\">\r\n                    <loading />\r\n                </div>\r\n            </div>\r\n        </div>\r\n    </div>\r\n</template>\r\n\r\n<script>\r\nimport PageHeading from \"../../components/Core/PageHeading.vue\";\r\nimport Loading from \"../../components/Core/Loading.vue\";\r\nimport { mapActions, mapGetters } from \"vuex\";\r\n\r\nexport default {\r\n    name: \"TalentsPage\",\r\n    components: { PageHeading, Loading },\r\n    async mounted() {\r\n        await this.getTalents();\r\n\r\n        this.parameters.page = this.TALENTS.meta.current_page;\r\n    },\r\n    data() {\r\n        return {\r\n            loading: false,\r\n            error: true,\r\n            parameters: {\r\n                page: 1,\r\n                sortBy: \"created_at\",\r\n                sortDir: \"desc\"\r\n            },\r\n            sortBySelection: [\r\n                { name: \"name\", dir: \"asc\" },\r\n                { name: \"name\", dir: \"desc\" },\r\n                { name: \"applied_position\", dir: \"asc\" },\r\n                { name: \"applied_position\", dir: \"desc\" },\r\n                { name: \"university\", dir: \"asc\" },\r\n                { name: \"university\", dir: \"desc\" },\r\n                { name: \"created_at\", dir: \"asc\" },\r\n                { name: \"created_at\", dir: \"desc\" }\r\n            ]\r\n        };\r\n    },\r\n    computed: {\r\n        ...mapGetters({\r\n            TALENTS: \"talent/TALENTS\"\r\n        })\r\n    },\r\n    methods: {\r\n        ...mapActions({\r\n            GET_TALENTS: \"talent/GET_TALENTS\"\r\n        }),\r\n        async changePage(direction) {\r\n            this.parameters.page = this.TALENTS.meta.current_page;\r\n\r\n            if (direction == \"next\") {\r\n                if (this.TALENTS.links.next) {\r\n                    this.parameters.page += 1;\r\n                    this.getTalents();\r\n                }\r\n            } else {\r\n                if (this.TALENTS.links.prev) {\r\n                    this.parameters.page -= 1;\r\n                    this.getTalents();\r\n                }\r\n            }\r\n        },\r\n        async getTalents() {\r\n            this.loading = true;\r\n            this.$toast.info(\"Loading...\");\r\n            try {\r\n                await this.GET_TALENTS({\r\n                    page: this.parameters.page,\r\n                    sort: this.parameters.sortBy,\r\n                    sortDir: this.parameters.sortDir\r\n                });\r\n                this.$toast.success(\"Data updated\");\r\n            } catch (err) {\r\n                this.$toast.error(\"Fail to fetched...\");\r\n            } finally {\r\n                this.loading = false;\r\n            }\r\n        },\r\n        sortTalents(name, dir) {\r\n            this.parameters.sortBy = name;\r\n            this.parameters.sortDir = dir;\r\n            this.parameters.page = 1;\r\n            this.getTalents();\r\n        },\r\n        currentSort(name, dir) {\r\n            return (\r\n                name == this.parameters.sortBy && dir == this.parameters.sortDir\r\n            );\r\n        }\r\n    }\r\n};\r\n</script>\r\n\r\n<style>\r\n.disabled {\r\n    pointer-events: none;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);
